<template>
  <div v-bind:class="{'instruction': 1, 'off': option}">Choose your conversion method</div>
  <form @submit.prevent="submit">
    <div class="tofrom">FROM</div>
    <div class="from">
      <button @click="chooseFrom" type="button" class="btn c" :disabled="from === 'f' || from === 'k'">
        Celsius
      </button>
      <button @click="chooseFrom" type="button" class="btn f" :disabled="from === 'c' || from === 'k'">
        Fahrenheit
      </button>
      <button @click="chooseFrom" type="button" class="btn k" :disabled="from === 'c' || from === 'f'">
        Kelvin
      </button>
    </div>
    <div class="tofrom">TO</div>
    <div class="to">
      <button @click="chooseTo" type="button" class="btn c" :disabled="from === 'c' || to === 'f' || to === 'k'">
        Celsius
      </button>
      <button @click="chooseTo" type="button" class="btn f" :disabled="from === 'f' || to === 'c' || to === 'k'">
        Fahrenheit
      </button>
      <button @click="chooseTo" type="button" class="btn k" :disabled="from === 'k' || to === 'c' || to === 'f'">
        Kelvin
      </button>
    </div>
    <input type="number" class="input" name="number" step="0.01" :disabled="!from && !to" />
    <button type="submit" class="submit" :disabled="!from && !to">Convert</button>
  </form>
  <div class="output">{{ answer }}</div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      from: "",
      to: "",
      answer: ""
    }
  },
  methods: {
    chooseFrom(e) {
      this.from = e.target.classList[1]
    },
    chooseTo(e) {
      this.to = e.target.classList[1]
    },
    submit(e) {
      let newVal, val = e.target.number.value
      if (this.from === 'f') {
        if (this.to === 'c') {
          newVal = ((val-32) * 5) / 9
        } else {
          newVal = (val-32) * 5/9 + 273.15
        }
      } else if (this.from === 'c') {
        if (this.to === 'f') {
          newVal = val * (9/5) + 32
        } else {
          newVal = val + 273.15
        }
      } else if (this.from === 'k') {
        if (this.to === 'c') {
          newVal = val - 273.15
        } else {
          newVal = (val - 273.15) * (9/5) + 32
        }
      }
      let firstType = this.from === 'c' ? '째 C' : this.from === 'f' ? ' 째F' : 'K'
      let secondType = this.to === 'c' ? '째 C' : this.to === 'f' ? '째 F' : 'K'
      this.answer = `${val} ${firstType} = ${newVal} ${secondType}`
      e.target.number.value = ''
      this.from = ''
      this.to = ''
    },
  },
}
</script>

